.wrapper {
  display: flex;
    justify-content: space-evenly;
    align-items: center;
   
    margin: 30px 0;
    padding: 8px;
    
    
}
.name {
  color: rgb(251 255 0);
  display: block;
margin: auto;
  padding: 8px;
  font-size: 30px;
}
.button {
  display: block;
margin-right: auto;
margin-left: auto;
  padding: 5px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: transparent;
  border: 1px solid white;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
  color: white;

  cursor: pointer;
}

.button:hover,
.button:focus {
  border: 1px solid blue;
  color: rgb(251 255 0);
}